@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&family=Unica+One&display=swap);
body{background:linear-gradient(90deg, #69b7eb, #b3dbd3, #f4d6db);padding:20px}a{color:#f7a5a7}.advent{display:flex;justify-content:center;flex-wrap:wrap}.advent__title{display:inline-block;width:100%;font-family:'Unica One', cursive;color:white;text-align:center;margin:80px 0px;font-size:48px;text-transform:uppercase}.advent__title--sub{font-size:24px;text-transform:none;margin:0px 0px 40px 0px}.advent__doors-container{flex:1 1;justify-content:center;display:grid;grid-gap:10px;grid-template-columns:repeat(1, 200px);grid-auto-rows:200px}@media (min-width: 768px){.advent__doors-container{grid-template-columns:repeat(3, 200px);grid-auto-rows:200px}}@media (min-width: 1024px){.advent__doors-container{grid-template-columns:repeat(4, 200px)}}.door{display:flex;align-items:center;justify-content:center;background:white;color:black;border-left:100px solid white;border-right:100px solid white;border-radius:20px;transition:border-width 1s ease;font-family:'Crimson Text', serif;font-size:24px;text-align:center;cursor:pointer}@media (min-width: 768px){.door{border-left:100px solid white;border-right:100px solid white}}.door__image{opacity:0;transition:2s opacity;width:0px}.door__image--fade{opacity:1;width:80%}.door--open{border-width:0;color:black}.modal{height:100vh;font-family:'Crimson Text', serif}.modal__container{min-width:90%;position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);z-index:100;border:1px solid gray;box-shadow:4px 4px 25px 1px #888;border-radius:2px}@media (min-width: 768px){.modal__container{min-width:60%}}.modal__header{width:100%;height:54px;border-bottom:2px solid white;cursor:'move';color:'white'}.modal__body{padding:50px;height:80%;color:#333333;font-size:14px}.modal__footer{width:100%;height:50px;border-top:2px solid white;text-align:right}.modal__close-button{color:#333333;position:absolute;right:5px;text-decoration:none;border:2px solid #333333;border-radius:8px;padding:10px;top:5px;cursor:pointer;font-family:'Crimson Text', serif}.snow{display:flex;justify-content:space-between;margin:0;padding:0;overflow:hidden;height:100vh;position:absolute;pointer-events:none;width:100vw}.snowflake{display:inline-block;width:0.1%;color:#fffafa;opacity:0;font-size:120px;margin:0;padding:0;animation:fall 16s linear infinite}@keyframes fall{0%{opacity:0}3%{opacity:0.9}90%{opacity:0.9}100%{transform:translate(0, 97vh);opacity:0}}

